.euclidienne{max-width:1000px;margin:0 auto;position:relative;border-radius:20px;box-shadow:0 10px 30px rgba(0,0,0,.3)}.euclidienne section{background:url("../../Image/cage_de_but.jpg") no-repeat;background-size:140%;background-position:center 20%;margin-bottom:50px;padding:30px 40px 60px;border-radius:15px;position:relative;overflow:hidden;height:600px;font-size:1.5rem;display:flex;flex-direction:column;justify-content:flex-end;align-items:flex-start}.euclidienne section:not(:last-child){border-bottom:2px solid hsla(0,0%,100%,.2);padding-bottom:40px}.euclidienne section div{width:100%;display:flex;flex-direction:column;align-items:flex-start;padding-right:20px}.euclidienne section p{position:relative;z-index:2;color:#000;background-color:hsla(0,0%,100%,.32);border-radius:5px;font-weight:bold;letter-spacing:.5px;padding:5px 10px;margin:5px 0 10px}.euclidienne section input,.euclidienne section textarea{width:70%;background:hsla(0,0%,100%,.484);backdrop-filter:blur(5px);border:2px solid #fff;border-radius:10px;padding:10px 20px;margin-bottom:15px;font-size:1.5rem;transition:all .3s ease}.euclidienne section input:focus,.euclidienne section textarea:focus{background:#fff;border-color:#3add21;box-shadow:0 0 15px rgba(58,221,33,.2)}.euclidienne section input.correct-answer,.euclidienne section textarea.correct-answer{border-color:#000;background:rgba(58,221,33,.1)}.euclidienne section h3{position:absolute;top:20px;left:20px;color:#fff;font-size:1.5em;padding:10px 20px;background:rgba(0,0,0,.5);border-radius:30px;z-index:3}.euclidienne section h3::before{content:"⚽";margin-right:10px}.euclidienne section .ball{position:absolute;top:auto;left:50%;bottom:20px;transform:translateX(-50%);width:40px;height:40px;opacity:0;transition:opacity .3s;z-index:3}.euclidienne section .ball::before{content:"⚽";font-size:40px;position:absolute;top:auto;left:0}.euclidienne section:hover .ball{opacity:1;animation:shootBallUp 1s forwards}.euclidienne section .problem-statement{width:100%;margin-bottom:20px}.euclidienne section .problem-statement .problem-text{background-color:rgba(0,0,0,.7);color:#fff;padding:15px;border-radius:10px;font-style:italic;margin-bottom:20px;border-left:4px solid #3add21}.euclidienne .score-display{background:hsla(0,0%,100%,.484);backdrop-filter:blur(5px);border:2px solid #fff;padding:20px;border-radius:15px;text-align:center}.euclidienne .score-display h3{font-size:2em;color:#1e5631;text-shadow:1px 1px 2px rgba(0,0,0,.3)}.euclidienne .score-display h3::before{content:"🏆";margin-right:10px}@keyframes shootBallUp{0%{transform:translate(-50%, 0) rotate(0deg);opacity:1}100%{transform:translate(-50%, -400px) rotate(360deg);opacity:0}}@media(max-width: 768px){input,textarea,div,h3{font-size:1rem}section{height:300px}.euclidienne section{margin-bottom:40px;padding:20px 15px 40px}}@media(max-width: 1024px){.euclidienne{margin:0 15px}.euclidienne section{height:auto;min-height:500px;padding:20px}.euclidienne section input,.euclidienne section textarea{width:80%}}@media(max-width: 768px){.euclidienne section{background-size:cover;min-height:400px;padding:15px}.euclidienne section input,.euclidienne section textarea{width:70%;font-size:1rem;padding:8px 15px}.euclidienne section p{font-size:.9rem;padding:4px 8px}.euclidienne section .ball{display:none}.euclidienne .score-display h3{font-size:1.5em}}@media(max-width: 480px){.euclidienne{margin:0 5px}.euclidienne section{min-height:300px;padding:15px 10px 30px;margin-bottom:30px;gap:10px}.euclidienne section h3{position:static;margin-bottom:10px;font-size:1rem;width:80%;text-align:center}}